// src/components/dashboard/AccountSection.jsx
"use client";
import { useState } from 'react';
import { useSession } from 'next-auth/react';
import { Form, Button, Alert } from 'react-bootstrap';

export default function AccountSection() {
  const { data: session } = useSession();
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [status, setStatus] = useState<'idle'|'loading'|'error'|'ok'>('idle');
  const [error, setError] = useState<string|null>(null);

  const handleSubmit = async e => {
    e.preventDefault();
    setStatus('loading');
    const res = await fetch('/api/auth/change-password', {
      method: 'POST',
      headers: { 'Content-Type':'application/json' },
      body: JSON.stringify({ currentPassword, newPassword }),
    });
    const payload = await res.json();
    if (!res.ok) {
      setError(payload.error);
      setStatus('error');
    } else {
      setStatus('ok');
      setError(null);
      setCurrentPassword('');
      setNewPassword('');
    }
  };

  return (
    <div className="text-center">
      <h3>Mi cuenta</h3>
      <p><strong>Nombre:</strong> {session.user.name}</p>
      <p><strong>Email:</strong> {session.user.email}</p>

      <hr/>

      <h5>Cambiar contraseÃ±a</h5>
      <Form onSubmit={handleSubmit} style={{maxWidth: '400px', margin:'0 auto'}}>
        {error && <Alert variant="danger">{error}</Alert>}
        {status==='ok' && <Alert variant="success">ContraseÃ±a actualizada</Alert>}

        <Form.Group className="mb-3">
          <Form.Label>ContraseÃ±a actual</Form.Label>
          <Form.Control
            type="password"
            required
            value={currentPassword}
            onChange={e => setCurrentPassword(e.target.value)}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Nueva contraseÃ±a</Form.Label>
          <Form.Control
            type="password"
            required
            value={newPassword}
            onChange={e => setNewPassword(e.target.value)}
          />
        </Form.Group>

        <Button type="submit" className="btn-orange w-100" disabled={status==='loading'}>
          {status==='loading' ? 'Guardandoâ€¦' : 'Guardar'}
        </Button>
      </Form>
    </div>
  );
}
