"use client";

import { useState, FormEvent, ChangeEvent, useEffect } from "react";
import { useSession } from "next-auth/react";
import { useRouter } from "next/navigation";
import Calendar from "react-calendar";
import "react-calendar/dist/Calendar.css";
import { Container, Form, Button, Alert, Row, Col } from "react-bootstrap";

const servicios = [
  "EstimulaciÃ³n en agua",
  "EstimulaciÃ³n en piso",
  "QuiroprÃ¡ctica",
  "Fisioterapia",
  "Masajes",
  "CosmetologÃ­a",
  "PrevenciÃ³n de lesiones",
  "PreparaciÃ³n fÃ­sica",
  "NutriciÃ³n",
  "Medicina en rehabilitaciÃ³n",
];

const terapeutas = [
  "JesÃºs RamÃ­rez",
  "Miguel RamÃ­rez",
  "Alitzel Pacheco",
  "Francia",
  "Gisela",
];

export default function ReservasForm() {
  const { data: session, status } = useSession();
  const router = useRouter();

  const [servicio, setServicio] = useState<string>("");
  const [terapeuta, setTerapeuta] = useState<string>("");
  const [fecha, setFecha] = useState<Date | null>(null);
  const [horario, setHorario] = useState<number | null>(null);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    if (status === "unauthenticated") {
      router.replace("/login");
    }
  }, [status, router]);

  const handleServicio = (e: ChangeEvent<HTMLSelectElement>) => {
    setServicio(e.target.value);
    setFecha(null);
    setHorario(null);
    setError(null);
  };

  const handleTerapeuta = (e: ChangeEvent<HTMLSelectElement>) => {
    setTerapeuta(e.target.value);
    setFecha(null);
    setHorario(null);
    setError(null);
  };

  // Firma exacta que espera react-calendar:
  const handleCalendarChange = (
    value: Date | Date[] | null,
    event: React.MouseEvent<HTMLButtonElement>
  ) => {
    if (value === null) {
      setFecha(null);
      setHorario(null);
      return;
    }
    const day = Array.isArray(value) ? value[0] : value;
    setFecha(day);
    setHorario(null);
    setError(null);
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();

    if (!servicio || !terapeuta || !fecha || horario === null) {
      setError("Completa todos los campos");
      return;
    }

    // AquÃ­ llamarÃ­as tu API para crear la reserva,
    // luego redirigir al checkout de Stripe...
    // await fetch("/api/appointments", { ... });
    // router.push("/checkout");

    console.log({ servicio, terapeuta, fecha, horario });
  };

  // Genera horas de 10 a 19
  const horas = Array.from({ length: 10 }, (_, i) => 10 + i);

  if (status === "loading" || !session) {
    return <Container className="py-5">Cargandoâ€¦</Container>;
  }

  return (
    <Container className="py-5" style={{ maxWidth: 600 }}>
      <h2 className="mb-4 text-center">Reservar cita</h2>
      {error && <Alert variant="danger">{error}</Alert>}
      <Form onSubmit={handleSubmit}>
        {/* Servicio */}
        <Form.Group className="mb-3">
          <Form.Label>Servicio</Form.Label>
          <Form.Select value={servicio} onChange={handleServicio} required>
            <option value="">Selecciona un servicio</option>
            {servicios.map((s) => (
              <option key={s} value={s}>
                {s}
              </option>
            ))}
          </Form.Select>
        </Form.Group>

        {/* Terapeuta */}
        <Form.Group className="mb-3">
          <Form.Label>Terapeuta</Form.Label>
          <Form.Select value={terapeuta} onChange={handleTerapeuta} required>
            <option value="">Selecciona un terapeuta</option>
            {terapeutas.map((t) => (
              <option key={t} value={t}>
                {t}
              </option>
            ))}
          </Form.Select>
        </Form.Group>

        {/* Fecha */}
        <Form.Group className="mb-4">
          <Form.Label>Fecha</Form.Label>
          <Calendar
            onChange={handleCalendarChange}
            value={fecha}
            minDate={new Date()}
            className="mb-3"
          />
        </Form.Group>

        {/* Horario */}
        {fecha && (
          <Form.Group className="mb-4">
            <Form.Label>Hora</Form.Label>
            <Row>
              {horas.map((h) => (
                <Col xs="4" md="3" className="mb-2" key={h}>
                  <Button
                    variant={horario === h ? "primary" : "outline-primary"}
                    className="w-100"
                    type="button"
                    onClick={() => {
                      setHorario(h);
                      setError(null);
                    }}
                  >
                    {`${h}:00`}
                  </Button>
                </Col>
              ))}
            </Row>
          </Form.Group>
        )}

        <Button type="submit" className="w-100 btn-orange">
          Confirmar y pagar
        </Button>
      </Form>
    </Container>
  );
}
